<template>
  <div class="detail">
    <p class="detail_p">用户详情</p>
    <div class="detail_xiang">
      <div>
        <p><span class="space">名称</span><span style="display: inline-block;margin-left: 10px">{{nickname}}</span></p>
        <p><span  class="space">余额</span><span style="display: inline-block;margin-left: 10px;color:#6098ff">¥ {{wallet}}</span></p>
        <p><span>注册时间</span><span>{{register_time}}</span></p>
        <p><span>是否开启</span><span>{{is_forbidden}}</span></p>
      </div>
      <div class="right">
        <p><span class="space_r">手机号</span><span style="margin-left: 10px">{{username}}</span></p>
        <p><span class="space_r">账号数</span><span style="margin-left: 10px">{{appMax}}</span></p>
        <p><span>最大账号数</span><span style="margin-left: 30px">{{appMaxnum}}</span></p>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        expense:'',
        is_forbidden:'',
        register_time:'',
        username:'',
        wallet:'',
        appMax:'',
        nickname:'',
        appMaxnum:'',

      }
    },
    methods: {
      getParticulars() {
        this.post("system/Info/getAgentById", {
          agent_id: this.$route.params.id
        }).then(res => {
          if(res.data.code === 10000) {
            this.expense = res.data.data.expense;
            this.nickname = res.data.data.nickname;
            this.wallet = res.data.data.wallet;
            this.appMax = res.data.data.app_num;
            this.register_time =new Date(res.data.data.register_time *1000).toLocaleString();
            this.username = res.data.data.username;
            this.appMaxnum = res.data.data.max_app_num;
            if(res.data.data.is_forbidden === 0){
              this.is_forbidden = "启用"
            }else{
              this.is_forbidden = "禁用"
            }

          } else {

          }
        });
      },

    },
    created() {
      this.getParticulars()
    },
  }
</script>

<style scoped>
  p{
    line-height: 2em;
    padding: 10px 0;
  }
  .detail_p {
    font-size: 26px;
  }
  .detail_xiang{
    display: flex;
  }
  .detail_xiang>div{
    flex: 0 0 400px;
  }
  .detail_xiang>div>p>span:nth-child(1){
    margin-left: 20px;
    font-size: 18px;
    color: #999999;
    position: relative;
  }
  .detail_xiang>div>p>span:nth-child(1):after{
    content: ':';
    position: absolute;
    left: 4.5em;
  }
  .detail_xiang>div>p>span:nth-child(2){
    text-align: left;
    display: inline-block;
    width:200px;
    margin-left: 45px;
    font-size: 18px;
  }
  .space{
    letter-spacing: 2em;
  }
  .space_r{
    letter-spacing: 1em;
  }
  .detail_xiang .right>p>span:nth-child(1):after{
    content: ':';
    position: absolute;
    left: 5.5em;
  }

</style>



